import React, { FunctionComponent, useContext } from "react";
import * as Io from "react-icons/io";
import * as Fa from "react-icons/fa";

import IconWrapper from "../../../utils/elementWrappers/IconWrapper";
import EllipsedText from "../../../utils/ellipsedText";
import CardOverlay from "../../../utils/elementWrappers/cardOverlay";
import { GuestPostsContext } from "./providerComponent";

const PostDetailsOverlay: FunctionComponent = () => {
  const { currentDetailedPost, setCurrentDetailedPost } = useContext(
    GuestPostsContext
  );
  if (currentDetailedPost === null || setCurrentDetailedPost === null) {
    return null;
  }
  const closeOverlay = () => setCurrentDetailedPost(null);
  return (
    <CardOverlay title={`Post Details`} closeOverlay={closeOverlay}>
      <small>
        <IconWrapper Icon={Io.IoIosChatboxes} />
        Conversation Topic
      </small>
      <p>{currentDetailedPost.heading}</p>
      <small>
        <IconWrapper Icon={Fa.FaPodcast} />
        Podcast
      </small>
      <EllipsedText>{currentDetailedPost.podcast.title}</EllipsedText>
      <small>
        <IconWrapper Icon={Io.IoIosInformationCircle} />
        Description
      </small>
      <p>{currentDetailedPost.description}</p>
    </CardOverlay>
  );
};

export default PostDetailsOverlay;
